 */
/**
 * @name List reset
 * @level X.X.
 * @description
 *   Display lists without the typical look of a list
 *
 * @code
 *   <ul class="u-list-reset">
 *     <li>Item 1</li>
 *     <li>Item 2</li>
 *   </ul>
 */
.u-list-reset {
  padding-left: 0;
  list-style-type: none; }

/**
 * @name Spacing
 * @level X.X.
 * @description
 *   Let elements "breath" with some spacing
 *
 * @code
 *   <div class="u-spacing-wrap">
 *     <div>No spacing top because I'm the first element</div>
 *     <div>I have spacing top</div>
 *     <div>Me too</div>
 *   </div>
 */
.u-spacing-wrap > * + * {
  margin-top: 1.5em; }

/**
 * Object
