 */
/* No files to import found in object//* */
/**
 * Component
 * Concrete, implementation-specific piece of UI
 */
/**
 * Block
 * 
 * @level X.X.
 */
.c-block {
  margin-top: 0.5em; }

/**
 * Code
 *
 * @level X.X.X.
 */
* + .c-block--code__code {
  margin-top: 1.5em; }
.c-block--code__code pre[class*='language-'] {
  margin: 0; }

/**
 * Color
 *
 * @level X.X.X.
 */
.c-block--color {
  margin-top: 1.5em; }

.c-block--color__set {
  padding-left: 0;
  list-style-type: none; }

.c-block--color__color {
  display: inline-block; }
  .c-block--color__color + .c-block--color__color {
    margin-left: 1.5em; }

.c-block--color__preview {
  width: 8em;
  height: 8em;
  border: 0.0625rem solid #bcbcbc; }

.c-block--color__name {
  margin: 0; }

/**
 * Variable
 *
 * @level X.X.X.
 */
.c-block--variable ul {
  padding-left: 0;
  list-style-type: none; }

/**
 * Footer main
 * 
 * @level X.X.
 */
.c-footer-main {
  padding: 0.5em 0;
  border-top: 0.0625rem solid #efefef; }

/**
 * Header main
 * 
 * @level X.X.
 */
.c-header-main {
  margin-bottom: 4em;
  padding: 2em 0;
  background: #35478C;
  text-align: center; }

.c-header-main__title, .c-header-main__description {
  color: #fff; }

.c-header-main__description {
  margin-top: 0.5em; }

/**
 * Section
 * 
 * @level X.X.
 */
.c-section-wrap > .c-section + .c-section {
  margin-top: 4em;
  padding-top: 4em;
  border-top: 0.0625rem solid #d5d5d5; }

.c-section--sub > * + * {
  margin-top: 3em; }
* + .c-section--sub {
  margin-top: 3em; }
p + .c-section--sub {
  margin-top: 1.5em; }
.c-section--sub .c-section--sub > * + * {
  margin-top: 2em; }

.c-section__anchor {
  position: absolute;
  margin-left: -0.75em;
  padding-right: 0.5em;
  text-decoration: none;
  opacity: 0;
  transition: opacity 0.2s; }
  .c-section__anchor, .c-section__anchor:visited, .c-section__anchor:hover, .c-section__anchor:focus {
    color: #d5d5d5; }
  .c-section__anchor:hover, .c-section__anchor:focus {
    text-decoration: underline; }
  .c-section__title:hover .c-section__anchor, .c-section__title:focus .c-section__anchor {
    opacity: 1; }

/**
 * TOC
 * 
 * @level X.X.
 */
.c-toc__list {
  list-style-type: none; }

.c-toc__list--main {
  padding: 0; }
  .c-toc__list--main > .c-toc__item + .c-toc__item {
    margin-top: 0.5em; }
  .c-toc__list--main > .c-toc__item > .c-toc__link {
    font-weight: 700;
    font-size: 1.25em; }

